let vendorPrefix: string | undefined;

const jsCssMap = {
  Webkit: '-webkit-',
  Moz: '-moz-',
  // IE did it wrong again ...
  ms: '-ms-',
  O: '-o-',
};

function getVendorPrefix(): string {
  if (vendorPrefix !== undefined) {
    return vendorPrefix;
  }
  vendorPrefix = '';
  const style = document.createElement('p').style;
  const testProp = 'Transform';
  for (const key in jsCssMap) {
    if (key + testProp in style) {
      vendorPrefix = key;
    }
  }
  return vendorPrefix;
}

function getTransitionName(): string {
  return getVendorPrefix()
    ? `${getVendorPrefix()}TransitionProperty`
    : 'transitionProperty';
}

function getTransformName(): string {
  return getVendorPrefix() ? `${getVendorPrefix()}Transform` : 'transform';
}

function setTransitionProperty(node: HTMLElement, value: string) {
  const name = getTransitionName();
  if (name) {
    node.style[name as any] = value;
    if (name !== 'transitionProperty') {
      node.style.transitionProperty = value;
    }
  }
}

function setTransform(node: HTMLElement, value: string) {
  const name = getTransformName();
  if (name) {
    node.style[name as any] = value;
    if (name !== 'transform') {
      node.style.transform = value;
    }
  }
}

function getTransitionProperty(node: HTMLElement) {
  return node.style.transitionProperty || node.style[getTransitionName() as any];
}

function getTransformXY(node: HTMLElement): { x: number, y: number } {
  const style = window.getComputedStyle(node, null);
  const transform =
    style.getPropertyValue('transform') ||
    style.getPropertyValue(getTransformName());
  if (transform && transform !== 'none') {
    const matrix = transform.replace(/[^0-9\-.,]/g, '').split(',');
    return {
      x: parseFloat(matrix[12] || matrix[4]),
      y: parseFloat(matrix[13] || matrix[5]),
    };
  }
  return {
    x: 0,
    y: 0,
  };
}

const matrix2d: RegExp = /matrix\((.*)\)/;
const matrix3d: RegExp = /matrix3d\((.*)\)/;

function setTransformXY(node: HTMLElement, xy: { x: number; y: number }): void {
  const style = window.getComputedStyle(node, null);
  const transform =
    style.getPropertyValue('transform') ||
    style.getPropertyValue(getTransformName());
  if (transform && transform !== 'none') {
    let arr;
    const match2d: RegExpMatchArray | null = transform.match(matrix2d);
    if (match2d) {
      const match2dStr = match2d[1];
      arr = match2dStr.split(',').map((item: string) => {
        return parseFloat(item);
      });
      arr[4] = xy.x;
      arr[5] = xy.y;
      setTransform(node, `matrix(${arr.join(',')})`);
    } else {
      const match3d = transform.match(matrix3d);
      if (!match3d) return;
      const match3dStr = match3d[1];
      arr = match3dStr.split(',').map(item => {
        return parseFloat(item);
      });
      arr[12] = xy.x;
      arr[13] = xy.y;
      setTransform(node, `matrix3d(${arr.join(',')})`);
    }
  } else {
    setTransform(
      node,
      `translateX(${xy.x}px) translateY(${xy.y}px) translateZ(0)`,
    );
  }
}

export {
  getTransformName,
  setTransitionProperty,
  getTransitionProperty,
  getTransformXY,
  setTransformXY,
};